import { useState, useEffect } from 'react';

// ‰∏¥Êó∂Á±ªÂûãÂÆö‰πâ
interface PromptTemplate {
  id: string;
  title: string;
  description: string;
  category: string;
  template: string;
}

export const usePromptTemplate = () => {
  const [templates, setTemplates] = useState<PromptTemplate[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    loadTemplates();
  }, []);

  const loadTemplates = async () => {
    try {
      setLoading(true);
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂÆûÈôÖÁöÑAPI
      // const templates = await dbApi.getPromptTemplates();
      // setTemplates(templates.data || []);
      
      // ‰∏¥Êó∂‰ΩøÁî®Á©∫Êï∞ÁªÑ
      setTemplates([]);
    } catch (error) {
      console.error('Failed to load templates:', error);
      setError('Âä†ËΩΩÊ®°ÊùøÂ§±Ë¥•');
    } finally {
      setLoading(false);
    }
  };

  const createTemplate = async (templateData: any) => {
    try {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂÆûÈôÖÁöÑÂàõÂª∫API
      console.log('Creating template:', templateData);
      await loadTemplates();
    } catch (error) {
      console.error('Failed to create template:', error);
      throw error;
    }
  };

  const updateTemplate = async (id: string, updates: Partial<PromptTemplate>) => {
    try {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂÆûÈôÖÁöÑÊõ¥Êñ∞API
      console.log('Updating template:', id, updates);
      await loadTemplates();
    } catch (error) {
      console.error('Failed to update template:', error);
      throw error;
    }
  };

  const deleteTemplate = async (id: string) => {
    try {
      // ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂÆûÈôÖÁöÑÂà†Èô§API
      console.log('Deleting template:', id);
      await loadTemplates();
    } catch (error) {
      console.error('Failed to delete template:', error);
      throw error;
    }
  };

  return {
    templates,
    loading,
    error,
    loadTemplates,
    createTemplate,
    updateTemplate,
    deleteTemplate
  };
};