"use client";

import React, { useState } from 'react';
import {
  Stack,
  Group,
  Title,
  Text,
  Button,
  Card,
  Badge,
  ActionIcon,
  Paper,
  Divider,
  Box,
  Timeline,
  Anchor,
  Progress,
  Stepper
} from '@mantine/core';
import {
  IconRoute,
  IconClock,
  IconTarget,
  IconTool,
  IconExternalLink,
  IconCheck,
  IconArrowRight,
  IconPlayerPlay,
  IconPlayerPause,
  IconRefresh,
  IconShare
} from '@tabler/icons-react';
import { FlowDesign, FlowStep } from '@my-platform/types';

interface FlowDesignViewerProps {
  flow: FlowDesign;
  onStepComplete?: (stepId: string) => void;
  onFlowComplete?: () => void;
  readonly?: boolean;
}

export const FlowDesignViewer: React.FC<FlowDesignViewerProps> = ({
  flow,
  onStepComplete,
  onFlowComplete,
  readonly = false
}) => {
  const [activeStep, setActiveStep] = useState(0);
  const [completedSteps, setCompletedSteps] = useState<Set<string>>(new Set());
  const [isExecuting, setIsExecuting] = useState(false);

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'green';
      case 'medium': return 'yellow';
      case 'hard': return 'red';
      default: return 'gray';
    }
  };

  const getDifficultyLabel = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'ÁÆÄÂçï';
      case 'medium': return '‰∏≠Á≠â';
      case 'hard': return 'Âõ∞Èöæ';
      default: return 'Êú™Áü•';
    }
  };

  const handleStepComplete = (step: FlowStep) => {
    const newCompleted = new Set(completedSteps);
    newCompleted.add(step.id);
    setCompletedSteps(newCompleted);
    
    onStepComplete?.(step.id);
    
    // Ëá™Âä®ËøõÂÖ•‰∏ã‰∏ÄÊ≠•
    if (activeStep < flow.steps.length - 1) {
      setActiveStep(activeStep + 1);
    } else {
      // ÊµÅÁ®ãÂÆåÊàê
      setIsExecuting(false);
      onFlowComplete?.();
    }
  };

  const getProgress = () => {
    return (completedSteps.size / flow.steps.length) * 100;
  };

  return (
    <Stack gap="lg">
      {/* ÊµÅÁ®ãÂ§¥ÈÉ®‰ø°ÊÅØ */}
      <Paper p="lg" withBorder>
        <Group justify="space-between" mb="md">
          <Group>
            <IconRoute size={32} color="var(--mantine-color-teal-6)" />
            <Box>
              <Title order={2}>{flow.title}</Title>
              <Text c="dimmed">{flow.description}</Text>
            </Box>
          </Group>
          
          <Group>
            <Badge color={getDifficultyColor(flow.difficulty)}>
              {getDifficultyLabel(flow.difficulty)}
            </Badge>
            {flow.totalTime && (
              <Badge variant="outline">
                <Group gap="xs">
                  <IconClock size={12} />
                  {flow.totalTime}
                </Group>
              </Badge>
            )}
            {!readonly && (
              <Button
                leftSection={isExecuting ? <IconPlayerPause size={16} /> : <IconPlayerPlay size={16} />}
                onClick={() => setIsExecuting(!isExecuting)}
                variant={isExecuting ? 'light' : 'filled'}
              >
                {isExecuting ? 'ÊöÇÂÅúÊâßË°å' : 'ÂºÄÂßãÊâßË°å'}
              </Button>
            )}
          </Group>
        </Group>

        {/* ËøõÂ∫¶Êù° */}
        {!readonly && (
          <Box>
            <Group justify="space-between" mb="xs">
              <Text size="sm" fw={500}>ÊâßË°åËøõÂ∫¶</Text>
              <Text size="sm" c="dimmed">
                {completedSteps.size} / {flow.steps.length} Ê≠•È™§ÂÆåÊàê
              </Text>
            </Group>
            <Progress value={getProgress()} size="lg" radius="md" />
          </Box>
        )}
      </Paper>

      {/* ÂâçÁΩÆÊù°‰ª∂ */}
      {flow.prerequisites && flow.prerequisites.length > 0 && (
        <Card withBorder>
          <Title order={4} mb="md">ÂâçÁΩÆÊù°‰ª∂</Title>
          <Stack gap="xs">
            {flow.prerequisites.map((prereq, index) => (
              <Group key={index} gap="xs">
                <IconCheck size={16} color="var(--mantine-color-green-6)" />
                <Text size="sm">{prereq}</Text>
              </Group>
            ))}
          </Stack>
        </Card>
      )}

      {/* ÊâßË°åÊ≠•È™§ */}
      <Card withBorder>
        <Title order={4} mb="md">ÊâßË°åÊ≠•È™§</Title>
        
        {readonly ? (
          // Âè™ËØªÊ®°ÂºèÔºö‰ΩøÁî®TimelineÂ±ïÁ§∫
          <Timeline active={-1} bulletSize={24} lineWidth={2}>
            {flow.steps.map((step, index) => (
              <Timeline.Item
                key={step.id}
                bullet={<Text size="xs">{index + 1}</Text>}
                title={step.title}
              >
                <Text size="sm" c="dimmed" mb="xs">
                  {step.description}
                </Text>
                
                <Group gap="xs" mb="sm">
                  {step.estimatedTime && (
                    <Badge size="xs" variant="outline">
                      È¢ÑËÆ° {step.estimatedTime}
                    </Badge>
                  )}
                  {step.difficulty && (
                    <Badge size="xs" color={getDifficultyColor(step.difficulty)}>
                      {getDifficultyLabel(step.difficulty)}
                    </Badge>
                  )}
                </Group>

                {step.resources && step.resources.length > 0 && (
                  <Stack gap="xs" mt="xs">
                    <Text size="sm" fw={500}>Áõ∏ÂÖ≥ËµÑÊ∫êÔºö</Text>
                    {step.resources.map((resource) => (
                      <Anchor
                        key={resource.id}
                        href={resource.url}
                        target="_blank"
                        size="sm"
                      >
                        <Group gap="xs">
                          <IconExternalLink size={12} />
                          {resource.title}
                        </Group>
                      </Anchor>
                    ))}
                  </Stack>
                )}

                {step.questions && step.questions.length > 0 && (
                  <Box mt="sm">
                    <Text size="sm" fw={500} mb="xs">Ê£ÄÊü•Ë¶ÅÁÇπÔºö</Text>
                    <Stack gap="xs">
                      {step.questions.map((question, qIndex) => (
                        <Text key={qIndex} size="sm" c="dimmed">
                          ‚Ä¢ {question}
                        </Text>
                      ))}
                    </Stack>
                  </Box>
                )}
              </Timeline.Item>
            ))}
          </Timeline>
        ) : (
          // ‰∫§‰∫íÊ®°ÂºèÔºö‰ΩøÁî®StepperÂ±ïÁ§∫
          <Stepper 
            active={activeStep} 
            onStepClick={setActiveStep}
            allowNextStepsSelect={false}
          >
            {flow.steps.map((step, index) => (
              <Stepper.Step
                key={step.id}
                label={`Ê≠•È™§ ${index + 1}`}
                description={step.title}
                completedIcon={<IconCheck size={16} />}
              >
                <Paper p="md" bg="gray.0" mt="md">
                  <Stack gap="md">
                    <Box>
                      <Title order={5} mb="xs">{step.title}</Title>
                      <Text size="sm" mb="md">{step.description}</Text>
                      
                      <Group gap="xs" mb="md">
                        {step.estimatedTime && (
                          <Badge variant="outline">
                            <Group gap="xs">
                              <IconClock size={12} />
                              {step.estimatedTime}
                            </Group>
                          </Badge>
                        )}
                        {step.difficulty && (
                          <Badge color={getDifficultyColor(step.difficulty)}>
                            {getDifficultyLabel(step.difficulty)}
                          </Badge>
                        )}
                      </Group>
                    </Box>

                    {step.resources && step.resources.length > 0 && (
                      <Box>
                        <Text size="sm" fw={500} mb="xs">Áõ∏ÂÖ≥ËµÑÊ∫êÔºö</Text>
                        <Stack gap="xs">
                          {step.resources.map((resource) => (
                            <Anchor
                              key={resource.id}
                              href={resource.url}
                              target="_blank"
                              size="sm"
                            >
                              <Group gap="xs">
                                <IconExternalLink size={12} />
                                {resource.title}
                                {resource.description && (
                                  <Text size="xs" c="dimmed">
                                    - {resource.description}
                                  </Text>
                                )}
                              </Group>
                            </Anchor>
                          ))}
                        </Stack>
                      </Box>
                    )}

                    {step.questions && step.questions.length > 0 && (
                      <Box>
                        <Text size="sm" fw={500} mb="xs">ÂÆåÊàêÊ£ÄÊü•Ôºö</Text>
                        <Stack gap="xs">
                          {step.questions.map((question, qIndex) => (
                            <Text key={qIndex} size="sm">
                              ‚Ä¢ {question}
                            </Text>
                          ))}
                        </Stack>
                      </Box>
                    )}

                    <Group justify="flex-end">
                      <Button
                        leftSection={<IconCheck size={16} />}
                        onClick={() => handleStepComplete(step)}
                        disabled={completedSteps.has(step.id)}
                        variant={completedSteps.has(step.id) ? 'light' : 'filled'}
                      >
                        {completedSteps.has(step.id) ? 'Â∑≤ÂÆåÊàê' : 'Ê†áËÆ∞ÂÆåÊàê'}
                      </Button>
                    </Group>
                  </Stack>
                </Paper>
              </Stepper.Step>
            ))}
          </Stepper>
        )}
      </Card>

      {/* È¢ÑÊúüÊàêÊûú */}
      {flow.outcomes && flow.outcomes.length > 0 && (
        <Card withBorder>
          <Title order={4} mb="md">È¢ÑÊúüÊàêÊûú</Title>
          <Stack gap="xs">
            {flow.outcomes.map((outcome, index) => (
              <Group key={index} gap="xs">
                <IconTarget size={16} color="var(--mantine-color-blue-6)" />
                <Text size="sm">{outcome}</Text>
              </Group>
            ))}
          </Stack>
        </Card>
      )}

      {/* Â∑•ÂÖ∑Ê∏ÖÂçï */}
      {flow.tools && flow.tools.length > 0 && (
        <Card withBorder>
          <Title order={4} mb="md">Êé®ËçêÂ∑•ÂÖ∑</Title>
          <Stack gap="sm">
            {flow.tools.map((tool) => (
              <Paper key={tool.id} p="sm" withBorder>
                <Group justify="space-between">
                  <Box style={{ flex: 1 }}>
                    <Group gap="xs" mb="xs">
                      <IconTool size={16} />
                      <Text fw={500}>{tool.name}</Text>
                      <Badge size="xs" variant="outline">
                        {tool.category}
                      </Badge>
                    </Group>
                    <Text size="sm" c="dimmed">
                      {tool.description}
                    </Text>
                  </Box>
                  <Anchor href={tool.url} target="_blank">
                    <ActionIcon variant="light">
                      <IconExternalLink size={16} />
                    </ActionIcon>
                  </Anchor>
                </Group>
              </Paper>
            ))}
          </Stack>
        </Card>
      )}
    </Stack>
  );
};